h1{
  font-size: 34px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
  padding-bottom: 12px;
}
h2{
  font-size: 29px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
  padding-bottom: 12px;
}
h3{
  font-size: 21px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
}
p{
  padding: 0px;
  margin: 0px;
}

.sml{
  display: block !important;
}
.sml-inline{
  display: inline-block !important;
}
.lrg{
  display: none !important;
}
.lrg-inline{
  display: none !important;
}
.align-flex-bottom{
  align-items: flex-end;
}
.white-text{
  color:rgb(246, 245, 242);
}
.right-align{
  float: right;
  }
.text-align-right{
  text-align: right;
  }
main{
  position:relative;
  height: 100%;
  width: 100%;
}
.initials{
  display: block;
  position: absolute;
  right: 30px;
  bottom: 24px;
  font-size: 110px;
  color: #f6ddda;
  line-height: 1em;
  z-index: -1;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  }
.content-top{
  padding-top: 144px !important;
  min-height: 100vh;
  /*max-height: 100vh;*/
  width: 100%;
  }
.content{
  position: relative;
  display: block;
  width: 100%;
  margin: 0px auto;
  padding: 24px 36px;
  }
.content.bottom{
  padding: 0px 36px 60px 36px;
  }
.full-screen-top {
  position: relative;
  top: 0px;
  margin-top: 0px;
  min-height: 100vh;
  max-height: 100vh;
  width: 100%;
  max-width: 100%;
  }
  .big-quote{
    font-size: 60px;
    line-height: 60px;
    color:#eabfba;
    font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  }

/* ---------- BOOTSTRAP ADJUSTMENTS ---------- */
.row{
  margin: 0px -5.5px;
  }
  .row>* {
    padding: 5.5px 5.5px;
  }


@media only screen and (min-width:768px){
.sml{
  display: none !important;
  }
.sml-inline{
  display: none !important;
  }
.lrg{
  display: block !important;
  }
.lrg-inline{
  display: inline-block !important;
  }
.content{
  padding: 24px 55px;
  }
.content.bottom{
  padding: 24px 55px 60px 55px;
  }
}
@media only screen and (min-width:992px){
h1{
  font-size: 55px;
  }
h2{
  font-size: 47px;
  }
h3{
  font-size: 34px;
  }
.content{
  max-width: 843px;
  padding: 24px 0px;
  }
.content.bottom{
  padding: 24px 0px 60px 0px;
  }
}
@media only screen and (min-width:1200px){
.content{
  max-width: 987px;
  }
  .big-quote{
    font-size: 72px;
    line-height: 72px;
  }  
}
@media only screen and (min-width:1400px){
h1{
  font-size: 76px;
  line-height: 64px;
  }
h2{
  font-size: 55px;
  line-height: 60px;
  }
h3{
  font-size: 47px;
  line-height: 60px;
  }
.content{
  max-width: 1220px;
  } 
.content-top{
  padding-top: 233px !important;
  }  
}
@media only screen and (min-width:1550px){
.content{
  max-width: 1364px;
  }
}
@media only screen and (min-width:1800px){
.content{
  max-width: 1597px;
  }
}

@media only screen and (max-width:786px){
  .content.no-pad-sml{
    padding: 0px;
  }
  .col-clear-sml{
    display: none;
  }
}